export const cloneAudioWorkletNodeOptions = audioWorkletNodeOptions => {
  return new Promise((resolve, reject) => {
    const _MessageChannel = new MessageChannel(),
      port1 = _MessageChannel.port1,
      port2 = _MessageChannel.port2;
    port1.onmessage = _ref => {
      let data = _ref.data;
      port1.close();
      port2.close();
      resolve(data);
    };
    port1.onmessageerror = _ref2 => {
      let data = _ref2.data;
      port1.close();
      port2.close();
      reject(data);
    };
    // This will throw an error if the audioWorkletNodeOptions are not clonable.
    port2.postMessage(audioWorkletNodeOptions);
  });
};